"use client"

import { useState, useEffect } from "react"
import { User, Lock, Trash2, Eye, EyeOff, Check } from "lucide-react"
import { useAuth } from "@/lib/auth-context"
import { useRouter } from "next/navigation"

export default function PerfilPage() {
  const { user, isLoading } = useAuth()
  const router = useRouter()
  const [activeTab, setActiveTab] = useState<"dados" | "senha" | "excluir">("dados")

  const [nome, setNome] = useState("")
  const [sobrenome, setSobrenome] = useState("")
  const [email, setEmail] = useState("")
  const [celular, setCelular] = useState("(11) 99999-0002")

  const [senhaAtual, setSenhaAtual] = useState("")
  const [novaSenha, setNovaSenha] = useState("")
  const [confirmarSenha, setConfirmarSenha] = useState("")
  const [showSenhaAtual, setShowSenhaAtual] = useState(false)
  const [showNovaSenha, setShowNovaSenha] = useState(false)

  const [confirmarExclusao, setConfirmarExclusao] = useState("")

  const [saved, setSaved] = useState(false)

  useEffect(() => {
    if (user) {
      setNome(user.nome || "")
      setSobrenome(user.sobrenome || "")
      setEmail(user.email || "")
      setCelular(user.celular || "")
    }
  }, [user])

  useEffect(() => {
    if (!isLoading && !user) {
      router.push("/auth/login")
    }
  }, [user, isLoading, router])

  if (isLoading) {
    return (
      <div className="bg-background border border-border rounded-xl p-12 text-center">
        <p className="text-foreground/60">Carregando...</p>
      </div>
    )
  }

  if (!user) {
    return (
      <div className="bg-background border border-border rounded-xl p-12 text-center">
        <p className="text-foreground/60">Redirecionando...</p>
      </div>
    )
  }

  const handleSaveDados = () => {
    setSaved(true)
    setTimeout(() => setSaved(false), 2000)
  }

  const handleChangeSenha = () => {
    if (novaSenha !== confirmarSenha) {
      alert("As senhas não coincidem")
      return
    }
    setSaved(true)
    setTimeout(() => setSaved(false), 2000)
    setSenhaAtual("")
    setNovaSenha("")
    setConfirmarSenha("")
  }

  const handleExcluirConta = () => {
    if (confirmarExclusao !== "EXCLUIR") {
      alert("Digite EXCLUIR para confirmar")
      return
    }
    alert("Conta excluída com sucesso (mockado)")
  }

  return (
    <div className="bg-background border border-border rounded-xl">
      {/* Tabs */}
      <div className="flex border-b border-border">
        <button
          onClick={() => setActiveTab("dados")}
          className={`flex items-center gap-2 px-6 py-4 text-sm font-medium transition-colors ${
            activeTab === "dados"
              ? "text-foreground border-b-2 border-primary"
              : "text-foreground/60 hover:text-foreground"
          }`}
        >
          <User size={18} />
          Meus Dados
        </button>
        <button
          onClick={() => setActiveTab("senha")}
          className={`flex items-center gap-2 px-6 py-4 text-sm font-medium transition-colors ${
            activeTab === "senha"
              ? "text-foreground border-b-2 border-primary"
              : "text-foreground/60 hover:text-foreground"
          }`}
        >
          <Lock size={18} />
          Alterar Senha
        </button>
        <button
          onClick={() => setActiveTab("excluir")}
          className={`flex items-center gap-2 px-6 py-4 text-sm font-medium transition-colors ${
            activeTab === "excluir" ? "text-red-500 border-b-2 border-red-500" : "text-foreground/60 hover:text-red-500"
          }`}
        >
          <Trash2 size={18} />
          Excluir Conta
        </button>
      </div>

      <div className="p-6">
        {/* Meus Dados Tab */}
        {activeTab === "dados" && (
          <div className="space-y-6">
            <div>
              <h2 className="text-xl font-medium text-foreground mb-2">Informações Pessoais</h2>
              <p className="text-sm text-foreground/60">Atualize suas informações de perfil</p>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-foreground mb-2">Nome</label>
                <input
                  type="text"
                  value={nome}
                  onChange={(e) => setNome(e.target.value)}
                  className="w-full px-4 py-3 border border-border rounded-lg bg-background focus:outline-none focus:border-accent"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-foreground mb-2">Sobrenome</label>
                <input
                  type="text"
                  value={sobrenome}
                  onChange={(e) => setSobrenome(e.target.value)}
                  className="w-full px-4 py-3 border border-border rounded-lg bg-background focus:outline-none focus:border-accent"
                />
              </div>
            </div>

            <div>
              <label className="block text-sm font-medium text-foreground mb-2">E-mail</label>
              <input
                type="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                className="w-full px-4 py-3 border border-border rounded-lg bg-background focus:outline-none focus:border-accent"
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-foreground mb-2">Celular</label>
              <input
                type="tel"
                value={celular}
                onChange={(e) => setCelular(e.target.value)}
                className="w-full px-4 py-3 border border-border rounded-lg bg-background focus:outline-none focus:border-accent"
                placeholder="(00) 00000-0000"
              />
            </div>

            <button
              onClick={handleSaveDados}
              className="flex items-center gap-2 bg-primary text-primary-foreground px-6 py-3 rounded-lg font-medium hover:bg-primary/90 transition-colors"
            >
              {saved ? <Check size={18} /> : null}
              {saved ? "Salvo!" : "Salvar Alterações"}
            </button>
          </div>
        )}

        {/* Alterar Senha Tab */}
        {activeTab === "senha" && (
          <div className="space-y-6 max-w-md">
            <div>
              <h2 className="text-xl font-medium text-foreground mb-2">Alterar Senha</h2>
              <p className="text-sm text-foreground/60">Atualize sua senha de acesso</p>
            </div>

            <div>
              <label className="block text-sm font-medium text-foreground mb-2">Senha Atual</label>
              <div className="relative">
                <input
                  type={showSenhaAtual ? "text" : "password"}
                  value={senhaAtual}
                  onChange={(e) => setSenhaAtual(e.target.value)}
                  className="w-full px-4 py-3 pr-12 border border-border rounded-lg bg-background focus:outline-none focus:border-accent"
                />
                <button
                  type="button"
                  onClick={() => setShowSenhaAtual(!showSenhaAtual)}
                  className="absolute right-4 top-1/2 -translate-y-1/2 text-foreground/50 hover:text-foreground"
                >
                  {showSenhaAtual ? <EyeOff size={18} /> : <Eye size={18} />}
                </button>
              </div>
            </div>

            <div>
              <label className="block text-sm font-medium text-foreground mb-2">Nova Senha</label>
              <div className="relative">
                <input
                  type={showNovaSenha ? "text" : "password"}
                  value={novaSenha}
                  onChange={(e) => setNovaSenha(e.target.value)}
                  className="w-full px-4 py-3 pr-12 border border-border rounded-lg bg-background focus:outline-none focus:border-accent"
                />
                <button
                  type="button"
                  onClick={() => setShowNovaSenha(!showNovaSenha)}
                  className="absolute right-4 top-1/2 -translate-y-1/2 text-foreground/50 hover:text-foreground"
                >
                  {showNovaSenha ? <EyeOff size={18} /> : <Eye size={18} />}
                </button>
              </div>
            </div>

            <div>
              <label className="block text-sm font-medium text-foreground mb-2">Confirmar Nova Senha</label>
              <input
                type="password"
                value={confirmarSenha}
                onChange={(e) => setConfirmarSenha(e.target.value)}
                className="w-full px-4 py-3 border border-border rounded-lg bg-background focus:outline-none focus:border-accent"
              />
            </div>

            <button
              onClick={handleChangeSenha}
              className="flex items-center gap-2 bg-primary text-primary-foreground px-6 py-3 rounded-lg font-medium hover:bg-primary/90 transition-colors"
            >
              {saved ? <Check size={18} /> : null}
              {saved ? "Senha Alterada!" : "Alterar Senha"}
            </button>
          </div>
        )}

        {/* Excluir Conta Tab */}
        {activeTab === "excluir" && (
          <div className="space-y-6 max-w-md">
            <div>
              <h2 className="text-xl font-medium text-red-500 mb-2">Excluir Conta</h2>
              <p className="text-sm text-foreground/60">
                Esta ação é irreversível. Todos os seus dados serão permanentemente excluídos.
              </p>
            </div>

            <div className="bg-red-50 border border-red-200 rounded-lg p-4">
              <p className="text-sm text-red-700">Ao excluir sua conta:</p>
              <ul className="mt-2 text-sm text-red-600 list-disc list-inside space-y-1">
                <li>Todos os seus pedidos serão desvinculados</li>
                <li>Seus endereços salvos serão removidos</li>
                <li>Você perderá acesso a todo histórico</li>
              </ul>
            </div>

            <div>
              <label className="block text-sm font-medium text-foreground mb-2">
                Digite <span className="font-bold">EXCLUIR</span> para confirmar
              </label>
              <input
                type="text"
                value={confirmarExclusao}
                onChange={(e) => setConfirmarExclusao(e.target.value)}
                className="w-full px-4 py-3 border border-red-300 rounded-lg bg-background focus:outline-none focus:border-red-500"
                placeholder="EXCLUIR"
              />
            </div>

            <button
              onClick={handleExcluirConta}
              disabled={confirmarExclusao !== "EXCLUIR"}
              className="bg-red-500 text-white px-6 py-3 rounded-lg font-medium hover:bg-red-600 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
            >
              Excluir Minha Conta
            </button>
          </div>
        )}
      </div>
    </div>
  )
}
